import p5 from "p5";

type Dimension = {
  width: number;
  height: number;
};

const ACC_RATE = 0.1;

const checkPos = (pos: number, mouse: number, acceleration: number): number => {
  if (pos <= mouse) return acceleration + ACC_RATE;
  if (pos >= mouse) return acceleration - ACC_RATE;
  return 0;
};

export const getParticles = (
  particlesAmount: number,
  radius: number,
  p5: p5
) => {
  let particles = new Array(particlesAmount);
  for (let i = 0; i < particlesAmount; i++) {
    particles[i] = new Particle({ width: radius, height: radius }, p5);
  }
  return particles;
};

let colors = ["red", "green", "blue", "purple", "orange"];

class Particle {
  dimension: Dimension;
  acceleration: p5.Vector;
  velocity: p5.Vector;
  p5: p5;
  position: p5.Vector;
  tail: Array<p5.Vector>;
  color: string;
  accLimit: number;

  constructor(dimension: Dimension, p5: p5) {
    this.dimension = dimension;
    this.p5 = p5;
    this.acceleration = this.p5.createVector();

    this.velocity = this.p5.createVector();
    this.position = this.p5.createVector(
      Math.random() * window.innerWidth,
      Math.random() * window.innerHeight
    );
    this.accLimit = Math.random() / 10;
    this.tail = [];
    this.color = colors[Math.floor(Math.random() * colors.length)] || "black";
  }

  update(mouseX: number, mouseY: number) {
    const { x, y } = this.position;
    this.acceleration = this.p5.createVector(
      checkPos(x, mouseX, this.acceleration.x), // - window.innerWidth / 2
      checkPos(y, mouseY, this.acceleration.y) //  - window.innerHeight / 2
    );
    this.acceleration.limit(this.accLimit); //(0.1);
    this.acceleration.setMag(0.08); //0.1
    this.velocity.add(this.acceleration);
    this.velocity.limit(3);
    this.acceleration.setMag(0.2);
    this.position.add(this.velocity);
    if (this.tail.length > 5) this.tail = this.tail.slice(0, -1);
    else this.tail = [this.position.copy(), ...this.tail];
  }

  drawCollider(mouseX: number, mouseY: number) {
    //this.p5.fill("red");
    this.p5.noFill();
    this.p5.rotateY(this.p5.millis() / 1000);
    this.p5.sphere(40, 100, 100); //
  }

  draw() {
    const { width, height } = this.dimension;
    this.p5.fill(this.color);
    this.p5.ellipse(this.position.x, this.position.y, width, height);
    for (let i = 0; i < this.tail.length; i++) {
      this.p5.ellipse(this.tail[i].x, this.tail[i].y, width - i, height - i);
    }
  }
}

export default Particle;
